
*{
   
  font-family: Arial, Helvetica, sans-serif;
  background: #fff;
}

body{
display: flex;
justify-content: center;
align-items: center;
overflow-x: hidden;
font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
}
h1{
  text-align: center;
  text-decoration: underline;
}

.cont{
position: relative;
}

.content-container{
display: flex;
justify-content: center;
gap: 70px;
flex-wrap: wrap;
margin-top: 30vh;
margin-left: 10px;
margin-right: 10px;
}

button{
padding: 10px 35px;
border: none;
background: rgb(42, 42, 98);
color: white;
font-size: 20px;
font-weight: 500;
border-radius: 5px;
position: relative;
transition: all .3s linear;
}
button:hover{
opacity: 0.5;
}

.link{
background-color: rgb(71, 71, 132);
width: 70%;
margin-top: 5rem;
opacity: 0;
visibility: hidden;
transition: all 0.3s linear;
z-index: 1;
}

.hoverme{
opacity: 1;
visibility: visible;
margin-top: 1px;
}

ul{
background-color: rgb(71, 71, 132);
}

ul li{
list-style-type: none;
font-size: 1rem;
padding: 10px;
background-color: rgb(71, 71, 132);
font-weight: 500;
cursor: pointer;
color: #fff;
}
ul li:hover{
opacity: 0.5;
}

.b2c{
position: absolute;
width: 70%;
top: 15rem;
left: 9rem;
box-shadow: 1px 3px 6px black;
opacity: 0;
visibility: hidden;
transition: all 0.3s linear;
z-index: 1;
}

.b2c2{
top: 3rem;
opacity: 1;
visibility: visible;

}
.last-list{
position: absolute;
width: 70%;
top: 17rem;
left: 9rem;
box-shadow: 1px 3px 6px black;
opacity: 0;
visibility: hidden;
transition: all 0.3s linear;
z-index: 1;
}

.active-list{
top: 7.5rem;
opacity: 1;
visibility: visible;
}



.planning{
background: rgb(133, 115, 226);

}
.desiging{
background: rgb(244, 57, 57);
}
.manufacturing{
background-color: rgb(182, 59, 80);
}
.marketing{
background: purple;
}

.plan, .design, .manu, .sales{
margin-top: 5rem;
opacity: 0;
visibility: hidden;
width: 70%;
background: rgb(133, 115, 226);
transition: all 0.3s linear;

}
.plan li{
background: rgb(133, 115, 226);
}
.design, .design li{
background: rgb(244, 57, 57);
}
.manu, .manu li{
background-color: rgb(182, 59, 80);
}
.sales, .sales li{
background-color: purple;
}

.hover-Plan, .hover-design, .manu-hover, .sales-hover{
 margin-top: 1px;
 opacity: 1;
 visibility: visible;
}
